export default "import { useRef, useState, MouseEvent, JSX } from 'react';\n\nimport { dsI18n, langToLocale } from '@skatteetaten/ds-core-utils';\nimport { TopBannerExternal, User } from '@skatteetaten/ds-layout';\nimport {\n  Business,\n  Paginated,\n  Person,\n  RolePicker,\n} from '@skatteetaten/ds-overlays';\n\nexport const RolePickerWithTopBannerExternalExample = (): JSX.Element => {\n  const modalRef = useRef<HTMLDialogElement>(null);\n  const [user, setUser] = useState<User>();\n\n  const handleLanguageClick = (e: MouseEvent<HTMLButtonElement>): void => {\n    const lang = e.currentTarget.lang;\n    dsI18n.changeLanguage(langToLocale[lang]);\n  };\n\n  const me: Person = {\n    name: 'Ola Nordmann',\n    personId: '10101012345',\n    type: 'Person',\n  };\n\n  const businesses: Paginated<Business> = {\n    total: 12,\n    list: [\n      {\n        name: 'Costco AS',\n        organizationNumber: '123456777',\n        isDeleted: false,\n        unitType: 'AS',\n        type: 'Organization',\n        subunits: [\n          {\n            name: 'Google ASA',\n            organizationNumber: '123456789',\n            isDeleted: false,\n            type: 'Organization',\n            unitType: 'ASA',\n          },\n          {\n            name: 'Facebook RHF',\n            organizationNumber: '123456790',\n            isDeleted: true,\n            type: 'Organization',\n            unitType: 'RHF',\n          },\n        ],\n      },\n      {\n        name: 'Instagram AS',\n        organizationNumber: '312843211',\n        isDeleted: true,\n        unitType: 'AS',\n        type: 'Organization',\n        subunits: [\n          {\n            name: 'Snapchat AS',\n            organizationNumber: '123456623',\n            isDeleted: true,\n            type: 'Organization',\n            unitType: 'AS',\n          },\n          {\n            name: 'Statoil ASA',\n            organizationNumber: '312849218',\n            isDeleted: false,\n            type: 'Organization',\n            unitType: 'ASA',\n          },\n        ],\n      },\n      {\n        name: 'Samsung DA',\n        organizationNumber: '312943218',\n        isDeleted: false,\n        type: 'Organization',\n        unitType: 'DA',\n      },\n      {\n        name: 'Toshiba AS',\n        organizationNumber: '312643218',\n        isDeleted: false,\n        type: 'Organization',\n        unitType: 'AS',\n      },\n      {\n        name: 'Hitachi AS',\n        organizationNumber: '312743218',\n        isDeleted: false,\n        type: 'Organization',\n        unitType: 'AS',\n      },\n      {\n        name: 'Vanguard AS',\n        organizationNumber: '332843218',\n        isDeleted: false,\n        type: 'Organization',\n        unitType: 'AS',\n      },\n      {\n        name: 'Amazon ASA',\n        organizationNumber: '112843218',\n        isDeleted: false,\n        type: 'Organization',\n        unitType: 'ASA',\n      },\n      {\n        name: 'Meta ANS',\n        organizationNumber: '212843218',\n        isDeleted: false,\n        type: 'Organization',\n        unitType: 'ANS',\n      },\n    ],\n  };\n\n  const people: Paginated<Person> = {\n    total: 4,\n    list: [\n      {\n        name: 'Antikvitet presis',\n        personId: '13889999726',\n        type: 'Person',\n        isDeleted: false,\n      },\n      {\n        name: 'Bønne elegant',\n        personId: '18849574503',\n        type: 'Person',\n        isDeleted: true,\n      },\n      {\n        name: 'Lomme filosofisk',\n        personId: '08889674513',\n        type: 'Person',\n        isDeleted: true,\n      },\n      {\n        name: 'Adelsmann varm',\n        personId: '14892449911',\n        type: 'Person',\n        isDeleted: false,\n      },\n    ],\n  };\n\n  return (\n    <>\n      <TopBannerExternal\n        user={user}\n        onLanguageClick={handleLanguageClick}\n        onLogInClick={\n          !user ? (): void => modalRef.current?.showModal() : undefined\n        }\n      >\n        {user && (\n          <TopBannerExternal.UserMenu\n            user={user}\n            notificationCount={1}\n            onLogOutClick={() => setUser(undefined)}\n            onSwitchUserClick={() => modalRef.current?.showModal()}\n          />\n        )}\n      </TopBannerExternal>\n      <RolePicker\n        ref={modalRef}\n        me={me}\n        businesses={businesses}\n        people={people}\n        onEntitySelect={async (entity) => {\n          let role: User['role'];\n          if (entity.name === me.name) {\n            role = 'meg';\n          } else if (entity.type === 'Organization') {\n            role = 'virksomhet';\n          } else {\n            role = 'andre';\n          }\n\n          setUser({\n            role: role,\n            name: entity.name,\n          });\n          modalRef.current?.close();\n        }}\n      />\n    </>\n  );\n};\n"